import React from "react";
import {NavLink} from "react-router-dom";
import styles from './Navbar.module.css';

export const NavLinks = () => {
    return (
        <nav className={styles.left__navMenu}>
            <div>
                {/*<NavLink to="/profile" activeClassName={styles.activeLink}></NavLink>*/}
                <NavLink to='/profile'
                         className={({isActive}) => isActive ? styles.activeLink : ''}
                >
                    
                </NavLink>
            </div>
            <div>
                {/*<NavLink to="/messages" activeClassName={styles.activeLink}></NavLink>*/}
                <NavLink to='/messages'
                         className={({isActive}) => isActive ? styles.activeLink : ''}
                >
                    
                </NavLink>
            </div>
            <div>
                {/*<NavLink to="/news" activeClassName={styles.activeLink}></NavLink>*/}
                <NavLink to='/news'
                         className={({isActive}) => isActive ? styles.activeLink : ''}
                >
                    
                </NavLink>
            </div>
            <div>
                {/*<NavLink to="/music" activeClassName={styles.activeLink}></NavLink>*/}
                <NavLink to='/music'
                         className={({isActive}) => isActive ? styles.activeLink : ''}
                >
                    
                </NavLink>
            </div>
            <div>
                {/*<NavLink to="/users" activeClassName={styles.activeLink}></NavLink>*/}
                <NavLink to='/users'
                         className={({isActive}) => isActive ? styles.activeLink : ''}
                >
                    
                </NavLink>
            </div>
            <div>
                {/*<NavLink to="/settings" activeClassName={styles.activeLink}></NavLink>*/}
                <NavLink to='/settings'
                         className={({isActive}) => isActive ? styles.activeLink : ''}
                >
                    
                </NavLink>
            </div>
        </nav>
    );
}